import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { MainViewComponent } from './main-view.component';


export const mainViewRoutes: Routes = [
    {
        path: '', component: MainViewComponent,
        children: [
            { path: '', redirectTo: 'statistic', pathMatch: 'full' },
            {
                path: 'catalog',
                loadChildren:
                    () => import('../catalog-view/catalog-view.module')
                        .then(mod => mod.CatalogViewModule)
            },
            {
                path: 'callcenter',
                loadChildren:
                    () => import('../callcenter-view/callcenter-view.module')
                        .then(mod => mod.CallcenterViewModule)
            },
        ]
    },
];


@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(mainViewRoutes),
        RouterModule
    ],
    declarations: []
})
export class LazyRouteModule {
}
